// ------------------------ Цена --------------------------
.price {
    max-width: 220px;
    .price__old {
        font-weight: 400;
        font-size: 14px;
        line-height: 14px;
        text-decoration-line: line-through;
        color: #7E7F81;
        white-space: nowrap;
        min-height: 12px;
    }
    .price__new {
        font-weight: 600;
        font-size: 40px;
        line-height: 40px;
        color: #000000;
        display: flex;
        align-items: center;
        white-space: nowrap;
        span {
            display: block;
            margin-left: 10px;
            font-weight: 700;
            font-size: 11px;
            line-height: 16px;
            padding: 8px 3px;
            border-radius: 50%;
            background: #C0DE48;
            display: flex;
            align-items: center;
            justify-content: center;
        }
    }
    &.price_mini {
        .price__new {
            font-weight: 600;
            font-size: 24px;
            line-height: 24px;
        }
        .price__old {
            font-weight: 400;
            font-size: 12px;
            line-height: 12px;
        }
    }
    .price__description {
        font-weight: 400;
        font-size: 12px;
        line-height: 16px;
        color: #7E7F81;
    }
}
// ------------------- Карточка -----
.model__content {
    display: flex;
    // min-width: 408px;
    text-decoration: none;
    @media (min-width: 992px) {
        margin-top: 31px;
    }
    &.model__content_vertical {
        flex-direction: column;
        @media (min-width: 576px) {
            flex-direction: row;
        }
        @media (min-width: 992px) {
            flex-direction: column;
        }
    }
    img {
        max-width: 155px;
        width: 100%;
        object-fit: contain;
        height: auto;
    }
}
.model__name {
    margin-top: 13px;
    font-weight: 600;
    font-size: 18px;
    line-height: 120%;
    color: #000;
}
.model__size {
    font-weight: 400;
    font-size: 14px;
    line-height: 16px;
    color: #B5B5B5;
    margin-top: 7px;
}
.model__stars {
    margin-bottom: 21px;
}
.model__bottom {
    display: flex;
    margin-top: 20px;
    justify-content: space-between;
    align-items: flex-end;
    button {
        max-width: 112px;
    }
}
.model__nal {
    margin-top: 10px;
}
.model__material {
    margin-bottom: 10px;
}
.model__nal,
.model__material {
    // display: none;
    opacity: 0;
    font-weight: 400;
    font-size: 12px;
    line-height: 150%;
    color: #7E7F81;
    transition: all .4s;
    height: 0;
    width: 0;
    overflow: hidden;
    span {
        margin-left: 5px;
        color: #000;
    }
}
.popular__model-title {
    font-weight: 600;
    font-size: 18px;
    line-height: 16px;
    color: #000;
}
.model__label {
    font-weight: 700;
    font-size: 11px;
    color: #fff;
    background: #EA122B;
    border-radius: 5px;
    padding: 0 7px;
    width: fit-content;
    &.model__label_black {
        background: #000000;
    }
}

// ------------------------ Звезды --------------------------
[data-id='stars'] {
    display: flex;
    align-items: center;
    .stars__count {
        font-weight: 600;
        font-size: 12px;
        line-height: 20px;
        color: #000;
    } 
}
.stars {
    width: 9px;
    height: 9px;
    background-repeat: no-repeat;
    background-size: contain;
    margin-right: 10px;
    &:last-child {
        margin-right: 0;
    }
    &.stars_full {
        background-image: url("data:image/svg+xml,%3Csvg width='9' height='9' viewBox='0 0 9 9' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M4.5 0.445312L5.66381 3.34346L8.77975 3.55474L6.38309 5.55717L7.14503 8.58589L4.5 6.92531L1.85497 8.58589L2.61691 5.55717L0.220246 3.55474L3.33619 3.34346L4.5 0.445312Z' fill='%23A9C43E'/%3E%3C/svg%3E%0A");
    }
    &.stars_empty {
        background-image: url("data:image/svg+xml,%3Csvg width='9' height='9' viewBox='0 0 9 9' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M4.5 0.445312L5.66381 3.34346L8.77975 3.55474L6.38309 5.55717L7.14503 8.58589L4.5 6.92531L1.85497 8.58589L2.61691 5.55717L0.220246 3.55474L3.33619 3.34346L4.5 0.445312Z' fill='%23D5D8CA'/%3E%3C/svg%3E%0A");
    }
}
// ------------------------ Кнопка купить --------------------------
button[data-id="openmodal"] {
    background: $green;
    border-radius: 5px;
    border: none;
    padding: 9px 23px;
    font-weight: 700;
    box-sizing: border-box;
    font-size: 13px;
    line-height: 13px;
    color: #FFFFFF;
    height: fit-content;
    display: flex;
    border: 1px solid $green;
    justify-content: center;
    // max-width: 112px;
    width: 100%;
    
    transition: .4s;
    &._big {
        max-width: unset;
        font-weight: 700;
        font-size: 16px;
        line-height: 16px;
        padding: 22px 41px;
    }
    p {
        margin: 0;
        position: relative;
        padding-right: 21px;
        white-space: nowrap;
        &::after {
            content: '+'; 
            position: absolute;
            color: white;
            top: 50%;
            transform: translateY(-50%);
            right: 0;
            font-weight: 700;
            font-size: 13px;
        }
    }
    &:hover {
        background: $darkgreen;
        border: 1px solid $darkgreen;
    }
}
// ------------------------ Кнопка консультация --------------------
button[data-id="callme"] {
    display: block;
    border: 1px solid $black;
    border-radius: 5px;
    padding: 9.5px 20px;
    background: none;
    transition: .4s;
    white-space: nowrap;
    height: fit-content;
    font-weight: 600;
    font-size: 15px;
    color: #000000;
    &:hover {
        border: 1px solid $green;
        color: $green;
    }
    &._big {
        font-weight: 700;
        font-size: 16px;
        line-height: 16px;
        padding: 22px 41px;
    }
}

// ------------------------ Заголовок --------------------------
h1.title, h2.title {
    font-family: 'Jost';
    font-style: normal;
    font-weight: 600;
    margin: 0;
    @include responsive-tlt(30px, 44px);
    &.title_year {
        display: flex;
        align-items: center;
        span {
            margin-left: 20px;
            font-family: 'ProximaNova';
            font-style: normal;
            font-weight: 700;
            font-size: 11px;
            line-height: 11px;
            padding: 4.5px 8.5px;
            border: 1px solid #000000;
            border-radius: 5px;
            height: fit-content;
        }
    }
    &.title_green {
        span {
            color: $green;
        }
    }
}
// ------------------------ Кнопка слайдер --------------------------
.slider__btn {
    border-radius: 50%;
    top: 50%;
    left: 0;
    transform: translateY(-50%);
    border: 1px solid #EEEEEE;
    z-index: 2;
    background-color: white;
    transition: .4s;
    @media (max-width: 576px) {
        display: none;
    }
    &.swiper-button-disabled {
        opacity: .2;
    }
    svg {
        fill: black;
    }
    &.slider__btn-next {
        left: unset;
        right: 0;
        svg {
            transform: scale(-1);
        }
    }
}


.main {
    
    @media (min-width: 1200px) {
        padding-top: 23px;    
    }
}
.welcome__container {
    display: flex;
    flex-direction: column;
    @media (min-width: 992px) {
        flex-direction: row;
        margin: 0 -15px;
    }
}
