    padding-left: 20px;
    padding-right: 20px;
    overflow: hidden;
    position: relative;
    @media (min-width: 768px) {
        padding-left: 88px;
    }
    h1 {
        max-width: 440px;
        font-family: 'Jost';
        font-style: normal;
        font-weight: 600;
        font-size: 44px;
        line-height: 100%;
        span {
            color: $green;
        }
    }
    p {
        max-width: 340px;
        width: 100%;
        font-weight: 400;
        font-size: 18px;
        line-height: 120%;
    }
    img {
        position: absolute;
        right: 11px;
        bottom: 18px;
        bottom: 0;
        object-fit: cover;
        z-index: -1;
    }
    ul {
        position: absolute;
        display: flex;
        color: white;
        left: 0;
        right: -50px;
        width: 105%;
        justify-content: flex-end;
        li {
            padding: 5px 25px;
            font-family: 'Jost';
            font-style: normal;
            font-weight: 500;
            font-size: 15px;
            line-height: 120%;
            position: relative;
            white-space: nowrap;
            &::after {
                content: '';
                position: absolute;
                right: 0;
                top: 50%;
                transform: translateY(-50%);
                width: 3px;;
                height: 3px;
                background: white;
                border-radius: 50%;
            }

        }
        &.banner__green-line {
            bottom: 18px;
            background: $green;
            transform: rotate(-10.23deg);
            z-index: 2;
        }
        &.banner__black-line {
            z-index: 1;
            bottom: -2px;
            background: #1B1E25;
            transform: rotate(-6.19deg);
        }
    }
}
.popular {
    margin: 0;
    border: 1px solid #DCE3E3;
    border-radius: 10px;
    width: 100%;
    padding: 12px 35px;
    max-width: 100%;
    z-index: 1;
    text-decoration: none;
    @media (min-width: 992px) {
        padding: 12px 35px 35px;
        margin: 0 15px;
        max-width: 300px;
    }
    .model__content {
        min-width: unset;
        img {
            max-width: 190px;
            margin: 0 auto;
            @media (min-width: 576px) {
                margin: 0;
            }
            @media (min-width: 992px) {
                margin: 0 auto;
            }
        }
    }
    .model__label {
        transform: translateY(-18px);
        @media (min-width: 576px) {
            transform: unset;
        }
        @media (min-width: 992px) {
            transform: translateY(-18px);
        }
    }
}

.benefits {
    list-style: none;
    display: flex;
    justify-content: center;
    padding: 21px;
    gap: 30px;
    flex-direction: row;
    justify-content: space-between;
    flex-wrap: wrap;
    // @media (min-width: 768px) {
    //     flex-direction: row;
    //     justify-content: space-between;
    // }

    li {
        font-weight: 600;
        font-size: 18px;
        line-height: 16px;
        color: #000;
        padding-left: 48px;
        position: relative;
        margin: 0 10px;
        // margin-bottom: 30px;
        
        &::after {
            content: '';
            position: absolute;
            width: 30px;
            height: 30px;
            left: 0;
            top: 50%;
            transform: translateY(-50%);
            border-radius: 50%;
            border: 1px solid #000;
            background-image: url("data:image/svg+xml,%3Csvg width='19' height='14' viewBox='0 0 19 14' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M7.92502 13.175L1.67502 6.92496C1.47024 6.68583 1.36323 6.37824 1.37538 6.06364C1.38753 5.74905 1.51795 5.45062 1.74057 5.22801C1.96319 5.00539 2.26161 4.87497 2.57621 4.86282C2.8908 4.85067 3.1984 4.95768 3.43752 5.16246L7.92502 9.64996L16.4 1.17496C16.6391 0.970179 16.9467 0.86317 17.2613 0.875322C17.5759 0.887473 17.8744 1.01789 18.097 1.24051C18.3196 1.46313 18.45 1.76155 18.4622 2.07614C18.4743 2.39074 18.3673 2.69833 18.1625 2.93746L7.92502 13.175Z' fill='%2318A865' stroke='white'/%3E%3C/svg%3E%0A");
            background-repeat: no-repeat;
            background-size: 17px 12px;
