        }
    }
}
.links {
    list-style: none;
    display: grid;
    grid-template-columns: repeat(auto-fit, 1fr);
    gap: 30px;
    @media (min-width: 576px) {
        grid-template-columns: repeat(auto-fit, minmax(330px, 1fr));
    }
    li {
        background: #F1F1F1;
        border-radius: 10px;
        min-height: 200px;
        font-weight: 600;
        font-size: 18px;
        line-height: 120%;
        padding: 42px 29px 33px;
        display: flex;
        background-repeat: no-repeat;
        background-size: cover;
        position: relative;
        z-index: 2;
        background-image: var(--bg-1);
        .link__content {
            width: 100%;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            @media (min-width: 768px) {
                margin-top: auto;
                flex-direction: row;
                align-items: center;
            }
        }
        img {
            position: absolute;
            bottom: 0;
            right: 5px;
            object-fit: contain;
            height: 100%;
            max-width: 209px;
            z-index: -1;
            @media (min-width: 768px) {
                right: 40px;
            }
        }
        p {
            display: block;
            width: min-content;
            margin: 0;
            width: min-content;
            margin-right: 20px;
        }
        a {
            padding: 9.5px 19.5px;
            background: #FFFFFF;
            border-radius: 5px;
            color: #000;
            text-decoration: none;
            transition: .4s;
            width: fit-content;
            margin-top: 20px;
            @media (min-width: 768px) {
                margin-left: auto;
                margin-top: 0;
            }
            &:hover {
                background: $green;
                color: #fff;
            }
        }
    }
}

.new-conllection {
    margin-top: 40px;

    .new-conllection__content {
        margin-top: 28px;
        display: flex;
        justify-content: space-between;
        gap: 30px;
        flex-direction: column;
        @media (min-width: 992px) {
            flex-direction: row;
        }
        .model__content {
            margin: 0 auto;
            margin-top: 0 !important;
            flex-direction: row-reverse;
            justify-content: space-evenly;
            max-width: 631px;
            width: 100%;
            padding: 20px 10px 0;
            
            border: 1px solid #DCE3E3;
            border-left: none;
            border-right: none;
            position: relative;
            min-width: auto;
            @media (min-width: 576px) {
                padding: 44px 10px 0;
                border-radius: 10px;
                border: 1px solid #DCE3E3;
            }
            @media (min-width: 992px) {
                min-height: 400px;
            }
            .model__discount {
                width: 55px;
                height: 55px;
                border-radius: 50%;
                background: #C0DE48;
                position: absolute;
                top: 10px;
                right: 10px;
                display: flex;
                align-items: center;
                justify-content: center;
                font-weight: 700;
                font-size: 14px;
                line-height: 16px;
                color: #000;
                @media (min-width: 576px) {
                    top: 70px;
                    right: 32px;
                }
            }
            img {
                height: fit-content;
                margin-top: auto;
                @media (min-width: 992px) {
                    min-width: 252px;    
                }
            }
            .model__info {
                max-width: 247px;
                display: flex;
                flex-direction: column;
                .model__name {
                    margin-top: 0;
                    padding-right: 10px;
                }
                .model__bottom {
                    margin-top: auto;
                    margin-bottom: 15px;
                    flex-wrap: wrap;
                    @media (min-width: 576px) {
                        margin-bottom: 37px;
                    }
                    @media (max-width: 1200px) {
                        flex-direction: column;
                        align-items: flex-start;
                    }

                }
            }

        }
    }
}
.about {
    background: #F8F8F8;
    position: relative;
    z-index: 2;
    &::after {
        content: '';
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        z-index: -1;
        height: 245px;
        background: white;
    }
}
.about__container {
    margin-top: 40px;
    display: flex;
    flex-direction: column;
    padding-bottom: 50px;
    border-bottom: 1px solid #EBEBEB;
    @media (min-width: 992px) {
        flex-direction: row;
        
    }
    .about__images {
        display: grid;
        gap: 15px;
        width: 100%;
        grid-template-columns: 1fr 1fr;
        @media (min-width: 992px) {
            gap: 30px;
        }
        @media (min-width: 1200px) {
            min-width: 631px;    
        }
        img {
            background: #ECECEC;
            width: 100%;
            
            border-radius: 10px;
            overflow: hidden;
            object-fit: cover;
            height: 100%;
            @media (min-width: 1200px) {
                min-height: 333px;
            }
            &:nth-child(1) {
                grid-column:  1/3;
            }
        }
    }
    .about__content {
        padding: 22px;
        width: 100%;
        @media (min-width: 992px) {
            padding: 22px 67px;
        }
        .title {
            margin-bottom: 22px;
        }
        p {
            font-weight: 400;
            @include responsive-tlt(16px, 18px);
            &.about__work-date {
                font-family: 'Jost';
                font-style: normal;
                font-weight: 600;
                font-size: 44px;
                line-height: 90%;
                color: $green;
                span {
                    margin-top: 0;
                }
            }
            span {
                display: block;
                font-weight: 300;
                font-size: 12px;
                line-height: 120%;
                color: #7E7F81;
                margin-top: 22px;
            }
        }
        h3 {
            font-family: 'Jost';
            font-style: normal;
            font-weight: 600;
            @include responsive-tlt(24px, 36px)
        }
    }
}
.about__bonus {
    padding: 65px 0;
    
    ul {
        display: flex;
        justify-content: center;
        flex-wrap: wrap;
        gap: 40px;
        li {
            font-weight: 600;
            font-size: 18px;
            line-height: 120%;
            padding-left: 111px;
            position: relative;
            max-width: 400px;
            width: 100%;
            span {
                display: block;
                font-weight: 400;
                font-size: 14px;
                line-height: 16px;
                color: #7E7F81;
                max-width: 300px;
            }
            .icon {
                position: absolute;
                left: 0;
                top: 50%;
                transform: translateY(-50%);
                width: 82px;
                height: 82px;
            }
        }
    }
}

.breadcrumbs {
    margin-top: 27px;
    margin-bottom: 16px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    ul {
        display: flex;
        flex-wrap: wrap;
        li {
            font-weight: 400;
            font-size: 12px;
            line-height: 16px;
            color: #7E7F81;
            position: relative;
            padding: 0 10px;
            &:first-child {
                padding-left: 0;
            }
            &::after {
                content: '/';
                font-weight: 400;
                font-size: 12px;
                position: absolute;
                line-height: 16px;
                color: $green;
                right: 0;
            }
            a {
                color: #7E7F81;
                text-decoration: none;
                transition: .4s;
                &:hover {
                    color: $green;
                }
            }
        }
    }
    button {
        border: none;
        background: unset;
        font-weight: 400;
        font-size: 12px;
        line-height: 16px;
        color: #B5B5B5;
        padding-left: 35px;
        position: relative;
        transition: .4s;
        &:hover {
            color: $green;
        }
        &::after {
            content: '';
            width: 19px;
            position: absolute;
            left: 0;
            top: 50%;
            height: 7px;
            transform: translateY(-50%);
            background-repeat: no-repeat;
            background-size: contain;
            background-image: url("data:image/svg+xml,%3Csvg width='20' height='8' viewBox='0 0 20 8' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M0.646446 3.64644C0.451185 3.84171 0.451185 4.15829 0.646446 4.35355L3.82843 7.53553C4.02369 7.73079 4.34027 7.73079 4.53553 7.53553C4.7308 7.34027 4.7308 7.02369 4.53553 6.82843L1.70711 4L4.53553 1.17157C4.7308 0.976309 4.7308 0.659727 4.53553 0.464465C4.34027 0.269203 4.02369 0.269203 3.82843 0.464465L0.646446 3.64644ZM20 3.5L1 3.5L1 4.5L20 4.5L20 3.5Z' fill='%23B5B5B5'/%3E%3C/svg%3E%0A");
        }
    }
}

.catalog {
    h1 {
        font-family: 'Jost';
        font-style: normal;
        font-weight: 600;
        font-size: 44px;
        line-height: 120%;
        color: #000;
    }
    ul {
        display: flex;
        align-items: center;
        gap: 10px;
        flex-wrap: wrap;
        li {
            font-weight: 400;
            font-size: 14px;
            line-height: 16px;
            transition: .4s;
            a {
                display: block;
                border: 1px solid #DCDFE2;
                border-radius: 80px;
                padding: 10px 20px;
                text-decoration: none;
                color: #000000;
                &._active, &:hover {
                    transition: .4s;
                    color: $green;
                    border-color: $green;
                }
            }
            &:first-child {
                color: #B5B5B5;
                margin-right: 8px;
            }
            &:last-child {
                margin-left: auto;
                border: none;
            }
        }
    }
    .catalog__content {
        margin-top: 20px;
        margin-bottom: 77px;
        display: grid;
        grid-template-columns: repeat(auto-fit, minmax(228px, max-content));
        justify-content: center;
    }
}

.detail {
    display: grid;
    grid-template-columns: 1fr;
    gap: 40px;
    @media (min-width: 992px) {
        grid-template-columns: 392px 1fr;
    }
    .detail__slider {
        position: relative;
        overflow: hidden;
        height: fit-content;
        height: 650px;
        .detail__slider-labels {
            z-index: 2;
            position: absolute;
            top: 30px;
            right: 9px;
            display: flex;
            flex-direction: column;
            gap: 6px;
        }
        .detail__item {
            position: relative;
            img {
                width: 100%;
                height: 100%;
                object-fit: contain;
                object-position: bottom;
                @media (min-width: 992px) {
                    object-position: top;
                }
            }
        }
        .detail__slider-btn {
            position: absolute;
            width: 40px;
            height: 40px;
        }
    }
    .detail__info {
        .detail__top {
            margin-top: 10px;
            padding-bottom: 19px;
            border-bottom: 1px solid #EAEDEF;
            display: flex;
            flex-direction: column;
            
            
            @media (min-width: 600px) {
                flex-direction: row;
                justify-content: baseline;
                flex-wrap: wrap;
                gap: 26px;
            }
            @media (min-width: 992px) {
                justify-content: space-between;
            }
            @media (min-width: 1200px) {
                justify-content: baseline;
            }
            p {
                margin: 0;
                font-weight: 400;
                font-size: 12px;
                line-height: 20px;
                color: #7E7F81;
                span {
                    padding: 0 5px;
                    color: #000000;
                }
            }
            p:last-child {
                position: relative;
                padding-left: 20px;
                margin-left: unset;
                @media (min-width: 600px) {
                    margin-left: auto;
                }
                @media (min-width: 992px) {
                    margin-left: unset;
                }
                @media (min-width: 1200px) {
                    margin-left: auto;
                }
                &::after {
                    content: '';
                    width: 14px;
                    height: 10px;
                    position: absolute;
                    left: 0;
                    top: 50%;
                    transform: translateY(-50%);
                    background-image: url("data:image/svg+xml,%3Csvg width='16' height='12' viewBox='0 0 16 12' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M14.9424 5.2215C15.2979 5.6865 15.2979 6.31425 14.9424 6.7785C13.8226 8.24025 11.1361 11.25 7.99964 11.25C4.86314 11.25 2.17664 8.24025 1.05689 6.7785C0.88393 6.55585 0.790039 6.28194 0.790039 6C0.790039 5.71806 0.88393 5.44415 1.05689 5.2215C2.17664 3.75975 4.86314 0.75 7.99964 0.75C11.1361 0.75 13.8226 3.75975 14.9424 5.2215V5.2215Z' stroke='%2318A865' stroke-width='1.5' stroke-linecap='round' stroke-linejoin='round'/%3E%3Cpath d='M8 8.25C9.24264 8.25 10.25 7.24264 10.25 6C10.25 4.75736 9.24264 3.75 8 3.75C6.75736 3.75 5.75 4.75736 5.75 6C5.75 7.24264 6.75736 8.25 8 8.25Z' stroke='%2318A865' stroke-width='1.5' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E%0A");
                    background-repeat: no-repeat;
                    background-size: contain;
                }
            }
        }
        .detail__center {
            padding: 18px 0;
            & > p {
                margin: 0;
                font-weight: 400;
                font-size: 13px;
                line-height: 16px;
                color: #7E7F81;
                span {
                    color: #000;
                }
            }
            & > div:nth-of-type(1) {
                display: flex;
                justify-content: space-between;
                padding-top: 12px;
                padding-bottom: 26px;
                flex-direction: column;
                @media (min-width: 1200px) {
                    flex-direction: row;
                }
                .detail__buttons {
                    display: flex;
                    gap: 16px;
                    margin-top: 20px;
                    flex-wrap: wrap;
                    @media (min-width: 576px) {
                        flex-wrap: nowrap;
                    }
                    @media (min-width: 1200px) {
                        margin-top: 0;
                    }
                    button[data-id="callme"] {
                        @media (max-width: 576px) {
                            width: 100%;
                        }
                    }
                    button[data-id="openmodal"] {
                        min-width: 300px;
                        width: 100%;
                    }
                }
            }
            & > div:nth-of-type(3) {
                margin-top: 35px;
                display: flex;
                flex-wrap: wrap;
                align-items: center;
                gap: 40px;
                padding: 22px 44px;
                border: 1px solid #E8E8E8;
                border-radius: 10px;
                a, button {
                    text-decoration: none;
                    font-weight: 400;
                    font-size: 14px;
                    line-height: 20px;
                    color: #000000;
                    position: relative;
                    display: flex;
                    transition: .4s;
                    &:hover {
                        color: $green;
                    }
                    .icon {
                        width: 15px;
                        height: 15px;
                        margin-right: 12px;
                    }
                    &::after {
                        content: '';
                        position: absolute;
                        left: 25px;
                        right: 0;
                        bottom: 0px;
                        width: auto;
                        height: 1px;
                        background: #18A865;
                    }
                }
                button {
                    
                    background: unset;
                    border: 0;
                    padding: 0;
                    @media (min-width: 1200px) {
                        margin-left: auto;
                    }
                    &::after {
                        background: #DDDEE1;
                    }
                }
            }
            .detail__color-tabs {
                display: flex;
                justify-content: space-between;
                align-items: center;
                gap: 30px;
                flex-wrap: wrap;
                p {
                    max-width: 320px;
                    font-weight: 400;
                    font-size: 12px;
                    line-height: 16px;
                    color: #7E7F81;
                    margin: 0;
                    a {
                        font-weight: 600;
                        color: #18A865;
                        transition: .4s;
                        &:hover {
                            color: $darkgreen;
                        }
                    }
                }
                ul {
                    list-style: none;
                    display: flex;
                    margin-top: 10px;
                    gap: 10px;
                    li {
                        width: 30px;
                        height: 30px;
                        border-radius: 50%;
                        padding: 7px;
                        position: relative;
                        cursor: pointer;
                        transition: .4s;
                        &::after {
                            transition: .4s;
                            position: absolute;
                            border-radius: 50%;
                            left: -7px;
                            right: -7px;
                            top: -7px;
                            bottom: -7px;
                        }
                        &:hover:not(._active) {
                            transform: scale(1.1);
                        }
                        &._active {
                            &::after {
                                content: '';
                                
                                border: 1px solid #18A865;
                            }
                            
                        }
                    }
                }
            }
            & > div:nth-of-type(4) {
                margin-top: 28px;
                h2 {
                    font-weight: 600;
                    font-size: 24px;
                    line-height: 120%;
                }
                .detail__haract {
                    margin-top: 9px;
                    @media (min-width: 600px) {
                        display: grid;
                        grid-template-columns: 50% 50%;                        
                    }
                    ul {
                        list-style: none;
                        li {
                            font-weight: 600;
                            font-size: 16px;
                            line-height: 180%;
                            span {
                                margin-left: 5px;
                                font-weight: 400;
                                word-break: break-word;
                            }
                            a {
                                font-weight: 400;
                                text-decoration: none;
                                color: $green;
                                transition: .4s;
                                &:hover {
                                    color: $darkgreen;
                                }
                            }
                        }
                    }
                    p {
                        font-weight: 400;
                        font-size: 16px;
                        line-height: 130%;
                    }
                }

            }
            & > div:nth-of-type(5) {
                padding-top: 22px;
                border-top: 1px solid #EAEDEF;
                ul {
                    display: flex;
                    gap: 40px;
                    flex-wrap: wrap;
                    justify-content: center;
                    align-items: center;
                    li {
                        font-weight: 600;
                        font-size: 16px;
                        line-height: 120%;
                        display: flex;
                        align-items: center;
                        .icon {
                            width: 52px;
                            height: 52px;
                            margin-right: 17px;
                        }
                        span {
                            color: $green;
                            position: relative;
                            padding-left: 20px;
                            &::after {
                                content: '';
                                position: absolute;
                                width: 10px;
                                height: 12px;
                                left: 5px;
                                top: 3px;
                                background-image: url("data:image/svg+xml,%3Csvg width='10' height='13' viewBox='0 0 10 13' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M5 0C2.24167 0 0 2.21882 0 4.94904C0 6.61521 0.975 8.00919 2.10833 8.9825C2.46667 9.28769 3.09167 9.77435 3.65 10.492C4.26667 11.2921 4.825 12.1499 5 12.7273C5.175 12.1499 5.73333 11.2921 6.35 10.492C6.90833 9.77435 7.53333 9.28769 7.89167 8.9825C9.025 8.00919 10 6.61521 10 4.94904C10 2.21882 7.75833 0 5 0ZM5 2.11159C5.37646 2.11159 5.74923 2.18498 6.09703 2.32758C6.44483 2.47017 6.76084 2.67918 7.02704 2.94266C7.29323 3.20614 7.50439 3.51894 7.64845 3.86319C7.79252 4.20745 7.86667 4.57642 7.86667 4.94904C7.86667 5.32166 7.79252 5.69063 7.64845 6.03488C7.50439 6.37914 7.29323 6.69193 7.02704 6.95542C6.76084 7.2189 6.44483 7.4279 6.09703 7.5705C5.74923 7.71309 5.37646 7.78648 5 7.78648C4.23971 7.78648 3.51056 7.48754 2.97296 6.95542C2.43536 6.42329 2.13333 5.70157 2.13333 4.94904C2.13333 4.1965 2.43536 3.47478 2.97296 2.94266C3.51056 2.41053 4.23971 2.11159 5 2.11159Z' fill='%2318A865'/%3E%3C/svg%3E%0A");
                                background-repeat: no-repeat;
                                background-size: contain;
                            }
                        }
                    }
                }
            }
        }
    }
